/* Makes the card have a thin gold border, and push together in flexbox. */
.card {
  border: 0.1rem solid gold;
  position: relative;
  text-align: left;
}

/* Defines image height */
.card > .img-container {
  height: 150px;
  overflow: hidden;
  text-align: center;
}

/* Defines image width */
.card > .img-container > img {
  width: 150px;
}
/* Sets the height, width, and flexboses all of the cards. */
.wrapper {
  height: 100%;
  width: 40%;
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-align-content: center;
          align-content: center;
  -webkit-justify-content: center;
          justify-content: center;
  overflow: auto;
}
/* ========================= */
/* Standard elements */
/* ========================= */

/* Chooses fonts, background elements, sets fonts to white */
body {
  font-family: 'ZCOOL QingKe HuangYou', Arial, Helvetica, sans-serif;
  background-color: black;
  font-size: 2vw;
  color: rgb(255, 255, 255);
  text-shadow: 2px 2px 3px black;
  text-shadow: 1px 1px 3px black;
  background-image: url(https://logos-download.com/wp-content/uploads/2016/09/Star_Wars_logo-1.png);
  background-size: 100%;
  background-repeat: no-repeat;
  background-position-y: 50%;
}

/* Restyles the buttons, reinforces the font */
button {
  width: 10vw;
  height: 5vw;
  font-size: 2vw;
  font-weight: bolder;
  background-color: gold;
  font-family: 'ZCOOL QingKe HuangYou', Arial, Helvetica, sans-serif;
}

/* ========================= */
/* Multiple Elements */
/* ========================= */

/* Sets basically all the app elemnts to identical widths and margins and borders et. al. */
.score, .title, .level, .game {
  width: 18vw;
  text-align: center;
  margin: 10px;
  position: fixed;
  border: 2px gold solid;
  padding: 5px;
}

/* Puts score and level boxes near the bottom */
.score, .level {
  bottom: 0vw;
}

/* Puts title and game backgrounds on them */
.title, .game {
  background: rgba(104, 104, 104, 0.7);
}

/* Puts the buttons in the middle of the screen */
.levelDown, .levelUp {
  position: fixed;
  top: 49%;
}

/* ========================= */
/* Custom Elements */
/* ========================= */

/* Adds background, left-aligns  */
.score {
  background: rgba(0, 170, 0, .70);
  left: 5%;
}

/* Adds background, right-aligns */
.level {
  right: 5%;
  background: rgba(0, 132, 181, .70);
}

/* Adds background, left-aligns */
.title {
  color: rgb(255, 255, 255);
  left: 5%;
}

/* Adds background, right-aligns */
.game {
  right: 5%;
  text-align: left;
}

/* Changes modal background */
.modalStyle {
  background-color: black;
}

/* Left-aligns */
.levelDown {
  left: 10%;
}

/* Right-aligns */
.levelUp {
right: 10%;
}

/* Defaults everything to border-box box-sizing */
* {
  box-sizing: border-box;
}

/* Defines font family here, too, because I am soaking wet. Also adds some dimensions and stuff. */
html, body, #root {
  font-family: 'ZCOOL QingKe HuangYou', Arial, Helvetica, sans-serif;
  padding: 0;
  margin: 0;
  height: 100%;
}
